import React from 'react'
import './LayoutStyle.css'
import Header from './Header'
import { StaticQuery } from 'gatsby'
import { Helmet } from "react-helmet"
import favicon from "../images/favicon-96x96.png"

class Layout extends React.Component{
    render(){
        return(
            
            <div className="layout__container">

                <StaticQuery query={graphql`
                query {
                    site {
                        siteMetadata {
                            title
                        }
                    }
              }`
            } render={data=>(
                
                    <Header logo={data.site.siteMetadata.title}/>
                   
                )}/>

            {this.props.children}
            <Helmet>
            <meta charSet="utf-8" />
            <meta property="og:title" content="Kamil Michna - blog programistyczny" />
            <meta property="og:url”"content="https://kamilmichna.github.io" />
            <meta property="og:type" content="website"/>
            <meta name="description" content="Blog opowiadający o IT wraz z portfolio. Opowiada o technologiach webowych, pojawiają się także tematy związane z innymi językami i innymi zagadnieniami IT. "/>
            <meta name="keywords" content="programowania, web development, front end, back end, Kamil Michna, Michna Kamil, programista Krosno, portfolio, javascript, ES6, js, koa.js, express.js"/>
            <title>Kamil Michna - blog programistyczny</title>
            <link rel="shortcut icon" type="image/png" href={favicon}/>
            </Helmet>   
            </div>
        
        )
    }
} 

export default Layout
